<template>
	<view class="content" :style="'height:' + windowHeight + 'px;'">
		<view class="coin-topview">
			<view class="wallet-title">
				我的钱包
			</view>
			<view class="wallet-img row">
				<view class="wallet-coin-middleView">
					<text class="wallet-coin">{{curMoney}}</text>
					<text class="wallet-yuelabel">我的余额</text>
				</view>
				<view class="wallet-coin-rightView" @click="doRecharge">去充值</view>
			</view> 

			<view style="width: 100%;margin-top: 15px;background-color: #f7f8f9;height: 10px;">
			</view>
			<view class="wallet-itemView row">
				<view @click="itemSeelcted(index)" v-for="(item,index) in itemArray" :key="index" class="wallet-item"
					:style=" index == selctedIndex ? 'color: #7900B2;' : 'color: #323232;'">
					{{item}}
				</view>
			</view>
			<view style="width: 100%;background-color: #f7f8f9;height: 52px;padding-left: 12px;" class="row"
				@click="timeSelected">
				<view style="height: 52px;line-height: 52px;font-size: 16px;color: #323232;">
					{{year}}年{{month}}月
				</view>
				<image src="../../../static/home-down.png" mode=""
					style="height: 20px;width: 20px;margin-top: 16px;margin-left: 3px;"></image>
			</view>
		</view>
		<scroll-view scroll-y="true" style="width: 100%;" :style="'height:' + scrollHeight + 'px;'">
			<view v-for="(item,index) in historyArray" :key="index" class="wallet-cell row">
				<view class="column" style="width: 50%;padding-left: 12px;">
					<view style="color: #323232; font-size: 16px;margin-top: 10px;">{{item.action_txt}}</view>
					<view style="color: #989898; font-size: 14px;margin-top: 5px;">{{item.add_time}}</view>
				</view>
				<view class="column" style="width: 50%;padding-right: 12px;">
					<view
						style="color: #323232; font-size: 18px;margin-top: 10px; font-weight: bold;width: 100%;text-align: right;"
						v-if="item.type == 1">
						{{'+' + item.total}}
					</view>
					<view
						style="color: #323232; font-size: 18px;margin-top: 10px; font-weight: bold;width: 100%;text-align: right;"
						v-else>
						{{'-' + item.total}}
					</view>
					<view
						style="color: #989898; font-size: 14px;margin-top: 5px;width: 100%text-align: right;text-align: right;">
						{{'余额:' + item.balance}}
					</view>
				</view>
			</view>
		</scroll-view>
		<view class="content" style="position: absolute; background-color: rgb(0,0,0,0.2);top: 0;left: 0;"
			v-show="showPicker">
			<view
				style="position: absolute;bottom: 0;width: 100%;height: 240px;background-color: #FFFFFF;border-radius: 10px 10px 0;">
				<view style="height: 40px;width: 100%;">
					<view class="wallet-cancleBtn" @click="cancle">
						取消
					</view>
					<view class="wallet-sureBtn" @click="changeDate">
						确定
					</view>
				</view>
				<picker-view indicator-style="height: 50px;" :value="value" @change="bindChange"
					style="width: 100%;height: 200px;margin-top: 0;">
					<picker-view-column>
						<view v-for="(item,index) in years" :key="index" class="picker-item">
							{{item}}
						</view>
					</picker-view-column>
					<picker-view-column>
						<view v-for="(item,index) in months[value[0]]" :key="index" class="picker-item">
							{{item}}
						</view>
					</picker-view-column>

				</picker-view>
			</view>

		</view>
	</view>
</template>

<script>
	export default {
		data() {
			const date = new Date()
			const years = []
			const year = date.getFullYear()
			years.push(year - 1)
			years.push(year)
			const months = []
			const month = date.getMonth() + 1
			const month1 = []
			const month2 = []
			for (let i = 1; i <= 12; i++) {
				month1.push(i)
			}
			months.push(month1)
			for (let i = 1; i <= month; i++) {
				month2.push(i)
			}
			months.push(month2)
			return {
				windowHeight: 0,
				scrollHeight: 0,
				historyArray: [],
				itemArray: ['消费记录', '充值记录', '退款记录'],
				selctedIndex: 0,
				value: [1, [month - 1]],
				years,
				months,
				month,
				year,
				showPicker: 0,
				selectTimeArray: [],
				curMoney: 0
			}
		},
		onLoad(option) {
			this.windowHeight = getApp().globalData.windowHeight - getApp().globalData.statusBarHeight - 44
			this.scrollHeight = this.windowHeight - 242
			this.curMoney = option.money
			console.log(this.months)
			this.requestData('')
		},
		methods: {
			timeSelected() {
				this.showPicker = 1;
			},
			itemSeelcted(index) {
				this.selctedIndex = index
				console.log(this.year, this.month, this.selctedIndex)

				this.requestData(this.year + '-' + this.month)
			},
			doRecharge() {
				uni.navigateTo({
					url: '../recharge/recharge'
				})
			},
			bindChange: function(e) {
				const val = e.detail.value
				console.log(e)

				if (this.value[0] != val[0]) {
					this.value = [val[0], 0]
				}
				this.selectTimeArray = val
			},
			cancle() {
				this.showPicker = 0;
			},
			changeDate() {
				this.showPicker = 0;
				this.year = this.years[this.selectTimeArray[0]]
				this.month = this.months[this.value[0]][this.selectTimeArray[1]]
				console.log(this.year, this.month)
				console.log(this.selectTimeArray)
				this.requestData(this.year + '-' + this.month)
			},
			requestData(time) {
				console.log(time)
				var that = this
				var type = ''
				if (that.selctedIndex == 0) {
					type = '2'
				} else if (that.selctedIndex == 1) {
					type = '1'
				} else {
					type = '3'
				}
				that.http.sendRequest('Balance.GetRecord', {
					'time': time,
					'p': 1,
					'type': type
				}).then(function(res) {
					if (res.code == 0) {
						that.historyArray = res.info
					}
				})
			}
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
	}

	.content {
		width: 100%;
		height: 100%;
		background-color: #F7F8F9;
	}

	.coin-topview {
		width: 100%;
		height: 280px;
		background-color: #FFFFFF;
	}

	.wallet-title {
		height: 38px;
		line-height: 38px;
		margin-left: 12px;
		color: #323232;
		font-size: 22px;
		font-weight: bold;
	}

	.wallet-img {
		margin-left: 12px;
		width: calc(100% - 24px);
		height: 104px;
		overflow: hidden;
		border-radius: 10px;
		background: url(./static/wallet-bj.png) no-repeat;
		background-size: 100%;
		/* background-color: #007AFF; */
		position: relative;
	}

	.wallet-itemView {
		width: 100%;
		height: 55px;
		background-color: #FFFFFF;
	}

	.wallet-item {
		width: 33.3%;
		height: 55px;
		font-size: 16px;
		line-height: 55px;
		text-align: center;
	}

	.wallet-cell {
		width: 100%;
		height: 74px;
		background-color: #FFFFFF;
		border-bottom: 1px solid #f5f6f7;
	}

	.wallet-coin-middleView {
		margin-left: 8px;
		width: calc(50%);
		height: 100%;
		/* background-color: #007AFF; */
		display: flex;
		flex-direction: column;
		flex: 1;
	}

	.wallet-coin {
		margin-top: 25px;
		height: 30px;
		line-height: 30px;
		font-size: 28px;
		font-weight: bold;
		color: #FFFFFF;
		margin-left: 20px;
	}

	.wallet-coin-rightView {
		/* position: absolute; */
		margin-right: 12px;
		width: 62px;
		height: 28px;
		margin-top: 38px;
		border-radius: 18px;
		text-align: center;
		background-color: #FFE3C7;
		color: #7900B2;
		font-size: 14px;
		line-height: 28px;
		font-weight: bold;
	}

	.wallet-yuelabel {
		height: 20px;
		line-height: 20px;
		font-size: 16px;
		color: #FFFFFF;
		margin-left: 20px;
		margin-top: 5px;
	}

	.wallet-cancleBtn {
		position: absolute;
		height: 40px;
		margin-top: 0;
		left: 10px;
		line-height: 40px;
		font-size: 14px;
		color: #989898;
		/* background-color: #007AFF; */
	}

	.wallet-sureBtn {
		position: absolute;
		height: 40px;
		margin-top: 0;
		right: 10px;
		line-height: 40px;
		color: #323232;
		font-size: 14px;
	}

	.picker-item {
		height: 50px;
		align-items: center;
		justify-content: center;
		text-align: center;
		line-height: 50px;
	}
</style>
